import React from 'react'

const Person = ({ person, setRemoveID }) => {
  return (
    <div className='pt-3 pd-3'>
      <p className='text-l'>{person.name}</p>
      <p className='text-l italic'>{person.number}</p>
      <button onClick={() => setRemoveID(person.id)} className='pl-1 pr-1 border-2 border-gray-400 rounded-sm ml-24'>
          delete
      </button>
    </div>
  )
}

export default Person